import { Component, Input  } from '@angular/core';

@Component({
  selector: 'dnv-key-value-pair',
  template: '<p><strong>{{key}}:</strong> {{value}}</p>',
})
export class DnvKeyValuePairComponent {
  @Input() key: string;
  @Input() value: string;
}

@Component({
  selector: 'dnv-key-value-list',
  template: '<div><dnv-key-value-pair *ngFor="let kv of kvpairs" [key]="kv[0]" [value]="kv[1]"></dnv-key-value-pair></div>',
})
export class DnvKeyValueListComponent {
  @Input() kvpairs: Array<[string, string]>;
}
